<template>
  <div>
    <div class="right-topTitle">
      {{$store.state.putInStorageInfo.stockInBillCode}}
    </div>
    <div class="right-content">
      <div class="right-content-body">
        <div style="padding: 5px" v-if="!toPutaway">
          <el-button size="mini" style="float: right;margin: 0px 10px 10px 10px;" type="primary" @click="putaway" v-if="$store.state.putInStorageInfo.stockInBillStatusKey == '3' || $store.state.putInStorageInfo.stockInBillStatusKey == '7'"></el-button>
          <el-table :data="recordList" border style="width: 100%">
            <el-table-column prop="materialCode" label="" align="center" :show-overflow-tooltip="true"></el-table-column>
            <el-table-column prop="materialName" label="" align="center" :show-overflow-tooltip="true"></el-table-column>
            <el-table-column prop="warehouseName" label="" align="center" :show-overflow-tooltip="true"></el-table-column>
            <el-table-column prop="storeAreaName" label="" align="center" :show-overflow-tooltip="true"></el-table-column>
            <el-table-column prop="goodsAllocationCode" label="" align="center" :show-overflow-tooltip="true"></el-table-column>
            <el-table-column prop="alreadyUpAmount" label="" align="center" :show-overflow-tooltip="true"></el-table-column>
            <el-table-column prop="measureUnit" label="" align="center" :show-overflow-tooltip="true"></el-table-column>
            <el-table-column prop="upShelfOperator" label="" align="center" :show-overflow-tooltip="true"></el-table-column>
            <el-table-column prop="upShelfTime" label="" align="center" :show-overflow-tooltip="true"></el-table-column>
          </el-table>
        </div>
        <div style="padding: 5px" v-else>
          <el-form :model="detailForm" ref="detailForm" :status-icon="true" :inline-message="true">
            <el-table :data="detailForm.waitPutAwayList" border style="width: 100%" class="operation-table">
              <el-table-column prop="materialCode" label="" align="center" :show-overflow-tooltip="true"></el-table-column>
              <el-table-column prop="materialName" label="" align="center" :show-overflow-tooltip="true"></el-table-column>
              <el-table-column prop="waitUpAmount" label="" align="center" :show-overflow-tooltip="true"></el-table-column>
              <el-table-column prop="upAmount" label="" align="center" :show-overflow-tooltip="true">
                <template slot-scope="scope">
                  <el-form-item label="" :prop="'waitPutAwayList.'+scope.$index+'.upAmount'" :rules="rules.amount">
                    <el-input size="mini" v-model="scope.row.upAmount" :max="scope.row.waitUpAmount" :min="0"></el-input>
                  </el-form-item>
                </template>
              </el-table-column>
              <el-table-column label="" align="center" :show-overflow-tooltip="true">
                <template slot-scope="scope">
                  <el-form-item label="" :prop="'waitPutAwayList.'+scope.$index+'.subareaCode'">
                    <el-select v-model="scope.row.subareaId" placeholder="" @change="subareaCodeChange(scope.row)" size="mini">
                      <el-option
                        v-for="item in storeAreaList"
                        :key="item.id"
                        :label="item.name"
                        :value="item.id">
                      </el-option>
                    </el-select>
                  </el-form-item>
                </template>
              </el-table-column>
              <el-table-column label="" align="center" :show-overflow-tooltip="true">
                <template slot-scope="scope">
                  <el-form-item label="" :prop="'waitPutAwayList.'+scope.$index+'.storagebinCode'">
                    <el-select v-model="scope.row.storagebinCode" placeholder="" size="mini">
                      <el-option
                        v-for="item in scope.row.goodsAllocationList"
                        :key="item.code"
                        :label="item.name"
                        :value="item.code">
                      </el-option>
                    </el-select>
                  </el-form-item>
                </template>
              </el-table-column>
            </el-table>
          </el-form>
          <div style="height: 40px">
            <el-button size="mini" style="float: right;margin: 10px;" type="primary" @click="save" v-if="detailForm.waitPutAwayList.length>0"></el-button>
            <el-button size="mini" style="float: right;margin: 10px;" type="primary" @click="save" v-else disabled></el-button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default{
    name:"Putaway",
    mounted(){
      let _this = this;
      this.id = this.$route.params.id;
      this.getRecordList();//
      this.getStoreAreas(this.$store.state.putInStorageInfo.warehouseId,function (result) {
        _this.storeAreaList = result;
      })
    },
    data(){
      var _this = this;
      var checkAmount = (rule, value, callback) => {
        if(_this.isNull(value)){
          callback();
        }
        let isNum = _this.isNumber(value)
        if (!isNum) {
          callback(new Error(''));
        } else {
          if(value < 0 ){
            callback(new Error('0'));
          }
          callback();
        }
      };
      return{
        id:"",
        toPutaway:false,
        recordList:[],//
        detailForm:{
          waitPutAwayList:[]//
        },
        storeAreaList:[],//
        goodsAllocationList:[],//
        rules:{
          amount:[
            { validator: checkAmount, trigger: 'change' }
          ]
        },
      }
    },
    methods:{
      getRecordList(){//
        this.$http.get("/ys-web-wms/putInStoragePutAway/getStockInUpShelfList?id="+this.id).then(function (result) {
          if(result.data.state == 200){
            this.recordList = result.data.data?result.data.data:[];
          }else{
            this.$notify.error({
              title:"",
              message:result.data.message
            })
            this.recordList = [];
          }
        });
      },
      getPutAwayList(){
        this.$http.get("/ys-web-wms/putInStoragePutAway/queryStockInDetailShelf?id="+this.id).then(function (result) {
          if(result.data.state == 200){
            let waitPutAwayList = result.data.data?result.data.data:[];
            let list = [];
            if(waitPutAwayList.length>0){
              for(var i=0;i<waitPutAwayList.length;i++){
                if(waitPutAwayList[i].waitUpAmount>0){
                  waitPutAwayList[i].upAmount = "";//
                  waitPutAwayList[i].subareaId = "";//id
                  waitPutAwayList[i].subareaCode = "";//
                  waitPutAwayList[i].storagebinCode = "";//
                  waitPutAwayList[i].goodsAllocationList = [];//
                  list.push(waitPutAwayList[i]);
                }
              }
            }
            this.detailForm.waitPutAwayList = list;
          }else{
            this.$notify.error({
              title:"",
              message:result.data.message
            })
            this.detailForm.waitPutAwayList = [];
          }
        })
      },
      subareaCodeChange(row){
        row.storagebinCode = "";
        let _this = this;
        this.getGoodsAllocations(row.subareaId,function (result) {
          row.goodsAllocationList = result;
          _this.getCodeById(row.subareaId,_this.storeAreaList,function (res) {
            row.subareaCode = res;
          })
        })
      },
      putaway(){
        this.getPutAwayList();//
        this.toPutaway = true;
      },
      save(){
        if(this.checkForm("detailForm")) {
          let list = this.detailForm.waitPutAwayList;
          let submitList = [];
          if (list.length > 0) {
            for (var i = 0; i < list.length; i++) {
              if (!this.isNull(list[i].upAmount)) {
                if(list[i].upAmount>list[i].waitUpAmount){
                  this.$message({
                    message: "" + list[i].materialName + "" + list[i].waitUpAmount,
                    type: "error",
                    showClose: true
                  })
                  return;
                }
                if (list[i].subareaId == "") {
                  this.$message({
                    message: "" + list[i].materialName + "",
                    type: "error",
                    showClose: true
                  })
                  return;
                }
                if (list[i].storagebinCode == "") {
                  this.$message({
                    message: "" + list[i].materialName + "",
                    type: "error",
                    showClose: true
                  })
                  return;
                }

                submitList.push(list[i]);
              }
            }
          }
          if (submitList.length > 0) {
            this.submit(submitList)
          }else{
            this.$message({
              message:"",
              type:"warning",
              showClose:true
            })
          }
        }
      },
      submit(list){
        let dataStr = JSON.stringify(list)
        this.$http.post("/ys-web-wms/putInStoragePutAway/putAway",{data:dataStr,id:this.id}).then(function (result) {
          if(result.data.state == 200){
            this.$message({
              message:"",
              type:"success",
              showClose:true
            })
            this.getRecordList();//
            this.toPutaway = false;
            let _this = this;
            this.getPutInStorageStatus(this.id,function (res) {
              _this.$store.state.putInStorageInfo.stockInBillStatusKey = res;
            })
          }else{
            this.$notify.error({
              title:"",
              message:result.data.message
            })
          }
        })
      }
    }
  }
</script>
<style>

</style>



